<template>
  <section>
    <div class="searchInpt clearfix">
      <select-input :title="title" :is-show.sync="listShow"></select-input>
      <!--sync 同步数据- :is-show.sync="listShow"-->
      <list v-show="listShow" :data="listData" @changeTitle="titleHandle"></list>
    </div>
  </section>
</template>

<style>

</style>

<script>
  import selectInput from '@/components/select-input'
  import list from '@/components/list'
  let listData=[
    {
      title:'javascript'
    },
    {
      title:'html+css'
    },
    {
      title:'java'
    },{
      title:'c#'
    }
  ]
  export default {
    data(){
      return {
          listShow:false,
          listData:listData,
          title:''
      }
    },
    methods:{
      titleHandle(title){
        console.log(title);
        this.title=title;
      }
    },
    components:{
      selectInput,
      list
    }
  }
</script>



