import Vue from 'vue'
import Vuex from 'Vuex'

Vue.use(Vuex);

let store = new Vuex.Store({
  state: {
    carPanelData: [],//用来存放购物车中的数据
    maxOff:false,
    carshow:false,
    carTimer:null,
    ball:{
      show:false,
      el:null,
      img:''
    }
  },
  getters: {//类似于计算属性computed
    totalCount(state) {
      let count = 0
      state.carPanelData.forEach((goods) => {//goods 从什么地方来的
        count += goods.count
      })
      return count
    },
    totalPrice(state) {
      let price = 0
      state.carPanelData.forEach((goods) => {
        price += goods.price * goods.count
      })
      return price
    },
    allChecked(state){
      let allChecked =true;
      state.carPanelData.forEach((goods)=>{
        if(!goods.checked){
          allChecked = false
          return
        }
      })
      return allChecked
    },
    checkedCount(state){
      let count=0
      state.carPanelData.forEach((goods)=>{
        if(goods.checked){
         count+=goods.count
          return
        }
      })
      return count
    },
    checkedPrice(state){
      let price=0
      state.carPanelData.forEach((goods)=>{
        if(goods.checked){
          price+=goods.count*goods.price
        }
      })
      return price
    },
    checkedGoods(state){
      let checkedGoods=[]
      state.carPanelData.forEach((goods)=>{
        if(goods.checked){
          checkedGoods.push(goods)
        }
      })
      return checkedGoods
    }


  },
  mutations: {
    addCarPanelDate(state, data) {//data 是谁引入过来的数据
      console.log(data);
      let bOff = true
      if(!state.ball.show){
        state.carPanelData.forEach((goods) => {
          if (goods.sku_id === data.info.sku_id) {
            goods.count+=data.count
            bOff = false
            if(goods.count>goods.limit_num){
              goods.count-=data.count
              state.maxOff=true
              return
            }
            state.carshow=true
            state.ball.show=true
            state.ball.img=data.info.ali_image
            state.ball.el=event.path[0]

          }
        })
        if (bOff) {//添加不重复的商品
          let goodsData = data.info
          Vue.set(goodsData, 'count', data.count)
          Vue.set(goodsData,'cheaked',true)
          state.carPanelData.push(goodsData)
          state.carshow=true
          state.ball.show=true
          state.ball.img=data.info.ali_image
          state.ball.el=event.path[0]
        }
      }
    },
    delCarPanelDate(state,id){
      state.carPanelData.forEach((goods,index)=>{
        if(goods.sku_id === id){
          state.carPanelData.splice(index,1)
          return
        }
      })
    },
    plusCarPanelData(state,id){
      state.carPanelData.forEach((goods,index)=>{
        if(goods.sku_id === id){
         if(goods.count>=goods.limit_num) return
          goods.count++
          return
        }
      })
    },
    subCarPanelData(state,id){
      state.carPanelData.forEach((goods,index)=>{
        if(goods.sku_id === id){
          if(goods.count<=1) return
          goods.count--
          return
        }
      })
    },
    checkGoods(state,id){
      state.carPanelData.forEach((goods,index)=>{
        console.log(goods);
        console.log(goods.sku_id);
        if(goods.sku_id === id){
          goods.checked =!goods.checked;
          console.log(goods.checked);
          return

        }
      })
    },
    allCheckGoods(state,allChecked){
      state.carPanelData.forEach((goods,index)=>{
        goods.checked=!allChecked
      })
    },
    delCheckGoods(state){
      let i =state.carPanelData.length
      while (i--){
        if (state.carPanelData[i].checked){
          state.carPanelData.splice(i,1)
        }
      }

    },
    closePrompt(state){
      state.maxOff=false
    },
    showCar(state){
      clearTimeout(state.carTimer)
      state.carshow=true
    },
    hideCar(state){
     state.carTimer= setTimeout(() =>{
        state.carshow=false
      },500)

    }
  }


})
export default store



