<template>
  <ul class="list-wrap">
    <li v-for="item in data" @click="getTitle(item.title)">{{item.title}}</li>
  </ul>
</template>

<style>

</style>

<script>
export default {
  props:['data'],
  methods:{
    getTitle(title){
      this.$emit('changeTitle',title)
    }
  }
}

</script>



