<template>
  <div >
    <form action="">
      <input type="text" :value="title" class="keyWord" @click="showListHandle">
    </form>
  </div>
</template>

<style>

</style>

<script>
export default {
  props:['isShow',"title"],
  computed:{
    initShow(){
      return this.isShow
    }
  },
  methods:{
    showListHandle(){
      console.log(this.initShow);
      this.$emit('update:isShow',!this.initShow)
    }
  }
}

</script>



